import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { HttpModule } from '@angular/http';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { AuthService } from '../app/services/auth.service'
import { ApiService } from '../app/services/api.service'
import { UserService } from '../app/services/user.service';
import { Push } from '@ionic-native/push';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { ArticlePage } from '../pages/article/article';
import { VideoPage } from '../pages/video/video';
import { ToolPage } from '../pages/tool/tool';
import { WebinarPage } from '../pages/webinar/webinar';
import { WebinarModalPage } from '../pages/webinar-modal/webinar-modal';
import { ResultsPage } from '../pages/results/results';
import { ConnectPage } from '../pages/connect/connect';
import { RatePage } from '../pages/rate/rate';
import { TermsConditionsPage } from '../pages/terms-conditions/terms-conditions';
import { ConnectPresenterPage } from '../pages/connect-presenter/connect-presenter';
import { WellbeingMinutePage } from '../pages/wellbeing-minute/wellbeing-minute';
import { BookingPage } from '../pages/booking/booking';
import { LiteHomePage } from '../pages/lite-home/lite-home';
import { LiteEapPage } from '../pages/lite-eap/lite-eap';
import { LiteAboutPage } from '../pages/lite-about/lite-about';
import { LiteBookPage } from '../pages/lite-book/lite-book';
import { LiteContactPage } from '../pages/lite-contact/lite-contact';
import { AppVersion } from '@ionic-native/app-version';
import { OneSignal } from '@ionic-native/onesignal';
import { SafeHtmlPipe } from '../pipes/safe-html/safe-html';
import { PushNotificationProvider } from '../providers/push-notification/push-notification';

 
@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    ArticlePage,
    VideoPage,
    ToolPage,
    WebinarPage,
    WebinarModalPage,
    ResultsPage,
    ConnectPage,
    RatePage,
    TermsConditionsPage,
    ConnectPresenterPage,
    WellbeingMinutePage,
    BookingPage,
    LiteHomePage,
    LiteEapPage,
    LiteAboutPage,
    LiteBookPage,
    LiteContactPage,
    SafeHtmlPipe
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    ArticlePage,
    VideoPage,
    ToolPage,
    WebinarPage,
    WebinarModalPage,
    ResultsPage,
    ConnectPage,
    RatePage,
    TermsConditionsPage,
    ConnectPresenterPage,
    WellbeingMinutePage,
    BookingPage,
    LiteHomePage,
    LiteEapPage,
    LiteAboutPage,
    LiteBookPage,
    LiteContactPage
  ],
  providers: [
    Push,
    StatusBar,
    SplashScreen,
    AuthService,
    ApiService,
    UserService,
    InAppBrowser,
    AppVersion, 
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    OneSignal,
    PushNotificationProvider
  ]
})
export class AppModule {}



